import router from "@/router";
import store from "@/store"

//  
router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requireAuth)) { // 
        let token = store.getters.getToken
        // token
        if (token) {
            next();
        } else {
            next({path: '/signIn'})
        }
    } else {
        next()
    }
})